<template>
  <Teleport to="body">
    <div class="fixed inset-0 z-50 overflow-y-auto">
      <!-- 背景遮罩 -->
      <div 
        class="fixed inset-0 bg-black/50 backdrop-blur-sm transition-opacity"
        @click="$emit('close')"
      ></div>
      
      <!-- 模态框内容 -->
      <div class="flex min-h-full items-center justify-center p-4">
        <div class="relative w-full max-w-2xl bg-white rounded-xl shadow-2xl animate-slide-in-up">
          <!-- 头部 -->
          <div class="flex items-center justify-between p-6 border-b border-gray-200">
            <div>
              <h3 class="text-lg font-semibold text-gray-900">会话详情</h3>
              <p class="text-sm text-gray-600">{{ session.session_id }}</p>
            </div>
            <button
              @click="$emit('close')"
              class="p-2 text-gray-400 hover:text-gray-600 hover:bg-gray-100 rounded-lg transition-colors"
            >
              <XMarkIcon class="w-5 h-5" />
            </button>
          </div>
          
          <!-- 内容 -->
          <div class="p-6 space-y-6">
            <!-- 基本信息 -->
            <div class="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div class="text-center p-3 bg-gray-50 rounded-lg">
                <div class="text-lg font-semibold text-gray-900">{{ formatDuration(session.duration) }}</div>
                <div class="text-xs text-gray-600">持续时间</div>
              </div>
              <div class="text-center p-3 bg-green-50 rounded-lg">
                <div class="text-lg font-semibold text-green-600">{{ session.success_count }}</div>
                <div class="text-xs text-gray-600">成功步骤</div>
              </div>
              <div class="text-center p-3 bg-red-50 rounded-lg">
                <div class="text-lg font-semibold text-red-600">{{ session.failed_count }}</div>
                <div class="text-xs text-gray-600">失败步骤</div>
              </div>
              <div class="text-center p-3 bg-blue-50 rounded-lg">
                <div class="text-lg font-semibold text-blue-600">{{ formatTime(session.start_time) }}</div>
                <div class="text-xs text-gray-600">开始时间</div>
              </div>
            </div>
            
            <!-- 对话内容 -->
            <div class="space-y-4">
              <!-- 用户输入 -->
              <div>
                <div class="flex items-center space-x-2 mb-2">
                  <UserIcon class="w-4 h-4 text-blue-500" />
                  <span class="text-sm font-medium text-gray-700">用户输入</span>
                </div>
                <div class="p-4 bg-blue-50 rounded-lg">
                  <p class="text-sm text-gray-800 leading-relaxed">{{ session.user_input }}</p>
                </div>
              </div>
              
              <!-- AI回复 -->
              <div>
                <div class="flex items-center space-x-2 mb-2">
                  <CpuChipIcon class="w-4 h-4 text-green-500" />
                  <span class="text-sm font-medium text-gray-700">AI回复</span>
                </div>
                <div class="p-4 bg-green-50 rounded-lg">
                  <p class="text-sm text-gray-800 leading-relaxed">{{ session.ai_response }}</p>
                </div>
              </div>
            </div>
            
            <!-- 操作按钮 -->
            <div class="flex items-center justify-between pt-4 border-t border-gray-200">
              <div class="flex items-center space-x-3">
                <button
                  @click="viewContext"
                  class="flex items-center space-x-2 px-4 py-2 text-sm bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
                >
                  <EyeIcon class="w-4 h-4" />
                  <span>查看上下文</span>
                </button>
                
                <button
                  @click="viewEvaluation"
                  class="flex items-center space-x-2 px-4 py-2 text-sm bg-purple-500 text-white rounded-lg hover:bg-purple-600 transition-colors"
                >
                  <ChartBarIcon class="w-4 h-4" />
                  <span>查看评估</span>
                </button>
              </div>
              
              <button
                @click="$emit('close')"
                class="px-4 py-2 text-sm text-gray-600 bg-gray-100 rounded-lg hover:bg-gray-200 transition-colors"
              >
                关闭
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Teleport>
</template>

<script setup lang="ts">
import { useRouter } from 'vue-router'
import { useAppStore } from '@/stores/app'
import { formatRelativeTime } from '@/utils/formatters'
import type { SessionData } from '@/types'
import {
  XMarkIcon,
  UserIcon,
  CpuChipIcon,
  EyeIcon,
  ChartBarIcon
} from '@heroicons/vue/24/outline'

interface Props {
  session: SessionData
}

const props = defineProps<Props>()

const emit = defineEmits<{
  close: []
}>()

const router = useRouter()
const appStore = useAppStore()

const formatDuration = (duration: number): string => {
  if (duration < 1) {
    return `${Math.round(duration * 1000)}ms`
  }
  return `${duration.toFixed(1)}s`
}

const formatTime = (timeStr: string): string => {
  try {
    const time = new Date(timeStr)
    return formatRelativeTime(time)
  } catch (error) {
    return '未知时间'
  }
}

const viewContext = () => {
  // 跳转到上下文查看器
  router.push(`/context?session=${props.session.session_id}`)
  emit('close')
}

const viewEvaluation = () => {
  // 跳转到异步评估展示
  router.push(`/evaluation?session=${props.session.session_id}`)
  emit('close')
}
</script>

<style scoped>
@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.animate-slide-in-up {
  animation: slideInUp 0.3s ease-out;
}
</style>